.clientList {
  margin: 0px;
  margin-top: 30px;
  background-color: green;
  width: 100%;
  padding: 0px;
}

.clientElement {
  margin-left: 65px;
}

.clientElement:hover {
  color: #2ac3e7;
}

.accordion {
  margin-top: 30px;
  margin-left: 0px;
  padding-left: 0px;
  width: 100%;
}


.toggleElement {
  margin: 0px;
  padding: 0px;
  background-color: #3f4d67;
  border: none;
  box-shadow: none;
  font-size: 20px;
  color: white;
  width: 100%;
}

.toggleElement:hover {
  background-color: #354157;
  color: #2ac3e7;
}


.active {
  content: '\e844';
  display: inline-block;
  width: 15px;
  transform-origin: center left;
  transform: rotate(90deg);
  transition-duration: 0.4s;
}

.unactive {
  width: 15px;
  transition-duration: 0.4s;
  transform-origin: center;
}
