@import url('https://fonts.googleapis.com/css2?family=Muli:wght@200;700&display=swap');

.globalContainer {
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 30px;
}

.dividongColumn {
  background-color: red;
  width: 10px;
}

.dashboardElement {
  height: 200px;
  width: 85%;
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  background-color: white;
  border-radius: 15px;
  border-style: none;
  box-shadow: 0px 2px 6px #a0a0a0;
}

.dashboardElement:hover {
  height: 200px;
  width: 85%;
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  background-color: white;
  border-radius: 15px;
  border-style: none;
  box-shadow: 0px 2px 15px #a0a0a0;
}

.dashboardBarChartGlobal {
  height: 450px;
  width: 92.5%;
  padding: 10px;
  padding-top: 15px;
  padding-left: 20px;
  background-color: white;
  border-radius: 15px;
  border-style: none;
  box-shadow: 0px 2px 6px grey;
}

.dashboardBarChart {
  height: 430px;
  width: 100%;
  margin-top: 0px;
  padding-bottom: 10px;
}

.dashboardPieChartGlobal {
  height: 200px;
  width: 92.5%;
  padding: 10px;
  padding-top: 15px;
  padding-left: 20px;
  background-color: white;
  border-radius: 15px;
  border-style: none;
  box-shadow: 0px 2px 6px #a0a0a0;
}

.dashboardPieChart {
  margin-top: -10px;
  height: 100%;
}

.bottomElement {
  width: 100%;
  position: absolute;
  bottom:0;
}

.pageTitle {
  color: #54b0e3;
  font-size: 28px;
  font-family: 'Muli', sans-serif;
  margin-bottom: 30px;
  width: 96.25%;
}

.barChartTitle {
  font-family: 'Muli', sans-serif;
  font-size: 15px;
  color: #666666;
}

.pieChartTitle {
  font-family: 'Muli', sans-serif;
  font-size: 15px;
  color: #666666;
  margin-bottom: 0px;
}

.dividerDiv {
  height: 50px;
}

.nivoBarStyle {
  font-family: 'Muli', sans-serif;
}

.percent {
  text-align: center;
  margin-top: -8%;
  font-family: 'Muli', sans-serif;
  font-size: 22px;
  font-weight: bold;
}
