.ClientTopBarContainer {
  height: 75px;
  width: 100%;
  background-color: white;
  box-shadow: 0px 0px 5px grey;
  z-index: 11;
  position: fixed;
  top: 0;
  transform: none;
}

.bootstrapNavbar {
  width: 100%;
}


.barElement {
  padding: 10px;
  margin-top: auto;
  margin-bottom: auto;
  vertical-align: middle;
}

.rightElements {
  text-align: right;
}

.leftTopBar {
  margin-left: 15px;
}

.customNavLink {
  align-self: flex-end!important;
  height: 75px;
}

.image {
   height: 50px;
}
