@import url('https://fonts.googleapis.com/css2?family=Muli:wght@200;700&display=swap');

.globalContainer {
  height: 100vh;
}

.leftImage {
  width: 100%;
  height: 100vh;
  padding: 0px;
  margin: 0px;
  z-index: 1;
}

.svgDiv {
  width: 100%;
  height: 100%;
  margin: 0px;
  padding: 0px;
}

.svg {
   object-fit: cover;
   width: 100%;
   height: 100%;
}

.rigthForm {
  display: inline-block;
  width: 500px;
  height: 100%;
  z-index: 10;
}

.centerText {
  text-align: center;
}

.title {
  color: #90c261;
  font-size: 35px;
  font-family: 'Muli', sans-serif;
  font-weight: bold;
  letter-spacing: 6px;
}

.subtitle {
  font-family: 'Muli', sans-serif;
  color: #a0a0a0;
  font-size: 18px;
}

.formElement {
  margin: 10px;
}

.submitButton {
  width: 200px;
  height: 50px;
  background-color: #90c261;
}

.submitButton:hover {
  background-color: #81ae57;
}

.centerComponent {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
