@import url('https://fonts.googleapis.com/css2?family=Muli:wght@200;700&display=swap');

.globalContainer {
  margin-left: 50px;
  margin-right: 50px;
  margin-top: 30px;
}

.btn-custom {
  font-size: 30px;
  border-color: #a0a0a0;
  border-width: 2px;
}

.FormElement {
  height: 100px;
  width: 85%;
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 40px;
  padding-bottom: 20px;
  background-color: white;
  border-radius: 15px;
  border-style: none;
  box-shadow: 0px 2px 6px #a0a0a0;
}

.formLabel {
  margin-left: 10px;
}

.graphElement {
  margin: 30px;
  margin-top: 0px;
  padding: 20px;
  width: 100%;
  background-color: white;
  border-radius: 15px;
  box-shadow: 0px 2px 6px #a0a0a0;
  color: #666666;
  font-size: 20px;
}

.divGraph {
  height: 480px;
}

.dateDropDownTitles {
  font-size: 15px;
  font-family: 'Muli', sans-serif;
}

.formCheck {
  margin-top: 8px;
  margin-bottom: 8px;
}

.dropdownDate {
  display:inline-block;
  width: 500px;
  margin: 10px;
  margin-left: 20px;
}

.dropdownElement {
  width: 100%;
  padding: 0px;
  margin: 0px;
}

.iconStyle {
   font-size: 40px;
   position: relative;
   top: 50%;
   transform: translateY(-50%);
}

.formItemTitle {
  color: #54b0e3;
  font-size: 15px;
  margin-bottom: 13px;
  font-family: 'Muli', sans-serif;
}

.pageTitle {
  color: #54b0e3;
  font-size: 28px;
  font-family: 'Muli', sans-serif;
  margin-bottom: 30px;
  width: 96.25%;
  transform: translateY(-10%);
}

.centerRefresh {
  position: relative;
  top: 50%;
  transform: translateY(-80%);
  padding: 0px;
}

.centerComponent {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
