@import url('https://fonts.googleapis.com/css2?family=Muli:wght@400;700&display=swap');

.global {
  display: inline-block;
  height: 100%;
  width: 100%;
}

.value {
  font-size: 42px;
  font-family: 'Muli', sans-serif;
}

.title {
  font-size: 15px;
  font-family: 'Muli', sans-serif;
}

.legend {
  font-size: 15px;
  font-family: 'Muli', sans-serif;
  color: #a0a0a0;
}

.centerComponent {
  text-align: center;
  height: 60%;
  position: relative;
  top: 50%;
  transform: translateY(-65%);
}
