@import url('https://fonts.googleapis.com/css2?family=Muli:wght@400;700&display=swap');

.imageDiv {
  margin: auto;
  padding: 10px;
  margin-bottom: 25px;
  margin-top: 20px;
}

.image {
   object-fit: cover;
   width: 100%;
   height: 60px;
}

.ClientSideBarContainer {
  height: 100vh;
  max-width: 16.666666666666666666667%;
  color: white;
  background-color: #90c261;
  box-shadow: 0px 0px 2px lightgrey;
  z-index: 10;
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  transform: none;
  padding: 0px;
  margin: 0px;
}

.ClientSideBarElement {
  display: inline;
  height: 70px;
  width: 100%;
  padding-top: 17px;
  padding-bottom: 20px;
  padding-left: 15px;
  font-size: 18px;
  font-family: 'Muli', sans-serif;
  font-weight: 500;
}

.iconDiv {
  width: 20%;
  height: 70px;
}

.iconDivWhenSelected {
  width: 18%;
  height: 70px;
}

.textElement {
  width: 78%;
  margin-left: 10px;
  margin-top: 200px;
}

.sameColorMark {
    margin-left: 6px;
    width: 2%;
    height: 70px;
}

.whiteSelectedMark {
  background-color: white;
  width: 2%;
  height: 70px;
}

.SelectedClientSideBarElement {
  display: inline;
  height: 70px;
  width: 98%;
  padding-top: 17px;
  padding-bottom: 20px;
  padding-left: 15px;
  background-color: #739b4d;
  font-size: 18px;
  font-family: 'Muli', sans-serif;
  font-weight: 400;
}

.SelectedClientSideBarElement:hover {
}

.ClientSideBarElement:hover {
  background-color: #81ae57;
}

.WhiteMark {
  background-color: white;
}
